<template>
  <div class="home">
     <b-row class="my-5">
      <b-col>
      <h1>Here you have some posts you may like</h1>
    </b-col>
    </b-row>
    <b-container>
  <b-row  align-v="center" >
    <posts v-for="post in posts" :key="post.id" :name="post.name" :srcimg="post.srcimg" :texto="post.texto" ></posts>
  </b-row>
</b-container>
  </div>
</template>

<script>
// @ is an alias to /src
import Posts from "@/components/Posts.vue";


export default {
  name: 'Home',
  components: {"posts": Posts},
    mounted() {
      this.fetchData();
    },
    data(){
      return {
        posts: []
      }
    },
    methods: {
      async fetchData(){
        const res =  await fetch("Posts.json");
        const val = await res.json();
        this.posts = val;
      }
    }
};
</script>

